@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;1,100;1,300&family=Roboto+Slab:wght@100;200;300;400;500&display=swap');

.contentContainer {
  width: 60vw;
  height: 7.925rem;
  margin: 2.313rem 6.25rem 0;
  padding: 2rem 9.188rem 1.625rem 1.688rem;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.div {
  padding: 1rem;
}

.title {
  font-family: RobotoSlab, sans-serif;
  font-size: 1.375rem;
  font-weight: bold;
  letter-spacing: -0.2px;
  margin-block-end: 0;
  margin-block-start: 0;
  color: #121212;
  text-align: left;
  margin: 0.25rem 0 0;
}

.genre {
  margin-block-end: 0;
  text-align: left;
  font-family: Montserrat, sans-serif;
  font-weight: bold;
  color: #121212;
  margin: 0.25rem 0 0;
}

.author,
.completed {
  height: 1.188rem;
  margin: 0.25rem 0 0;
  font-family: RobotoSlab, sans-serif;
  font-size: 0.875rem;
  font-weight: 300;
  margin-block-start: 0;
  letter-spacing: normal;
  color: #4386bf;
  text-align: center;
  text-align: left;
}

.content1FirstChild {
  margin-left: 5px;
}

.content1SecondChild {
  padding: 0;
  margin-top: 5px;
  align-self: flex-end;
}

.content1SecondChild button {
  border: none;
  background-color: #fff;
  font-size: 0.875rem;
  font-weight: 300;
  color: #4386bf;
}

.content2 {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
}

.line {
  border-right: 1px solid #e8e8e8;
  background-color: #e8e8e8;
  width: 0.125rem;
  height: 4.375rem;
}

.circular-progressbar {
  font-size: 2px;
  width: 60px;
  height: 60px;
}

.percentage {
  font-size: 40px;
}

.content3 {
  display: flex;
  flex-direction: column;
}

.currentChapter {
  opacity: 0.5;
  font-family: RobotoSlab, sans-serif;
  font-size: 0.813rem;
  font-weight: 300;
  color: #121212;
  text-align: left;
}

.chapter {
  font-family: RobotoSlab, sans-serif;
  font-size: 1rem;
  font-weight: 300;
  letter-spacing: -0.4px;
  margin-top: 12px;
  color: #292929;
  text-align: left;
  opacity: 0.8;
}

.content1SecondChild .removeBtn {
  border-left: 1px solid #e8e8e8;
  border-right: 1px solid #e8e8e8;
  cursor: pointer;
}

.updateBtn {
  width: 11.5rem;
  height: 2.063rem;
  border-radius: 3px;
  background-color: #0290ff;
  text-align: center;
  border: none;
  margin-top: 22px;
  font-family: RobotoSlab, sans-serif;
  color: #fff;
}
